<template>
  <v-container ref="form">
    <v-text-field
      label="Monday"
      outlined
      v-model="availability.monday">
    </v-text-field>
    <v-text-field
      label="Tuesday"
      outlined
      v-model="availability.tuesday">
    </v-text-field>
    <v-text-field
      label="Wednesday"
      outlined
      v-model="availability.wednesday">
    </v-text-field>
    <v-text-field
      label="Thursday"
      outlined
      v-model="availability.thursday">
    </v-text-field>
    <v-text-field
      label="Friday"
      outlined
      v-model="availability.friday">
    </v-text-field>
    <v-text-field
      label="Saturday"
      outlined
      v-model="availability.saturday">
    </v-text-field>
    <v-text-field
      label="Sunday"
      outlined
      v-model="availability.sunday">
    </v-text-field>
    <v-btn
      block
      color="primary"
      elevation="2"
      @click="this.onSubmit"
      v-if="this.question.answered===false"
    >Continue
    </v-btn>
  </v-container>
</template>

<script>
export default {
  props: {
    tutor:Object,
    question:Object
  },
  data () {
    return {
      availability: {
        monday: '',
        tuesday: '',
        wednesday: '',
        thursday: '',
        friday: '',
        saturday: '',
        sunday: ''
      },
      errors: {
        monday: false,
        tuesday: false,
        wednesday: false,
        thursday: false,
        friday: false,
        saturday: false,
        sunday: false
      }
    }
  },
  watch:{
    availability: {
      handler(value) {
        this.$emit("changedAvail",value)
      },
      deep: true
    }
  },
  methods:{
    onSubmit(){
      this.$emit('answered',4)
      this.$emit("expandDrawer",5)
    }
  }

}
</script>

<style>
.errormsg{
  margin-top:-10px !important;
}
</style>