<template>
  <v-container>
    <v-radio-group
      v-model="interested"
      row
      @change="changed"
    >
      <v-radio
        label="Yes"
        :value="true"
      ></v-radio>
      <v-radio
        label="No"
        :value="false"
      ></v-radio>
    </v-radio-group>
    <v-btn
      block
      color="secondary"
      elevation="2"
      @click="this.onSubmit"
      v-if="interested===false"
    >
      Submit Form
    </v-btn>
    <v-btn
      block
      color="primary"
      elevation="2"
      @click="this.onSubmit"
      v-else-if="interested===true&&this.question.answered===false"
    >
      Continue
    </v-btn>
    <v-btn
      block
      color="primary"
      elevation="2"
      disabled
      v-else-if="this.question.answered===false"
    >
      Continue
    </v-btn>
  </v-container>
</template>

<script>
export default {
  props: {
    tutor:Object,
    question:Object
  },
  data () {
    return {
      interested: null
    }
  },
  methods:{
    onSubmit(){
      this.$emit("changedInterest",this.interested)
      if(this.interested){
        this.$emit('answered',0)
        this.$emit("expandDrawer",1)
      }
      else{
        this.$emit("submitForm")
      }
    },
    changed(){
      this.$emit("changedInterest",this.interested)
    }
  }
}
</script>

