<template>
  <v-container>
    <v-textarea
      solo
      name="input-7-4"
      v-model="misc"
      label="Enter additional information here"
    ></v-textarea>
    <v-btn
      block
      color="primary"
      elevation="2"
      @click="this.onSubmit"
      v-if="this.question.answered===false"
    >Continue
    </v-btn>
  </v-container>
</template>

<script>
export default {
  props: {
    tutor:Object,
    question:Object
  },
  watch:{
    misc: function(value) {
      this.$emit("changedMisc",value)
    }
  },
  methods:{
    onSubmit(){
      this.$emit('answered',5)
      this.$emit('review')
    }
  },
  data () {
    return {
      misc: ""
    }
  },

}
</script>

