<template>
  <div>
    <v-expansion-panels 
      expand-icon
      tile
      mandatory
      :value="position">
      <v-expansion-panel
        v-for="(question,i) in enabledQuestions"
        :key="i"
        class="pa-4"
        :class="question.disabled ? 'grey--text text--lighten-3' : ''"
        :disabled="question.disabled"
      >
        <v-expansion-panel-header
          :disable-icon-rotate="question.answered"
          >
          <v-container class="d-flex justify-start">
            <h3 class="pr-10">{{i+1}}</h3>
            <h3
            class="flex-grow-1">
            {{question.title}}
            </h3>
          </v-container>
          <template v-slot:actions>
            <v-icon v-if="question.answered" color="teal">
              mdi-check
            </v-icon>
          </template>
        </v-expansion-panel-header>
        <v-expansion-panel-content>
          <component 
            :is="question.type" 
            :question="question" 
            :tutor="tutor" 

            @changedInterest="changedInterest"
            @changedRoles="changedRoles"
            @changedLearningCenter="changedLearningCenter"
            @changedCourses="changedCourses"
            @changedAvail="changedAvail"
            @changedMisc="changedMisc"

            @checkValidation="checkValidation"
            @answered="answered"
            @expandDrawer="expandDrawer"
            @submitForm="submitForm"
            @review="review"
            >
          </component>
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>
    <v-btn
      block
      color="secondary"
      elevation="2"
      v-if="showReview"
      @click="overlay=true"
      class="mt-6"
      :disabled="!this.enableReview"
    >Review and Submit
    </v-btn>
    <v-dialog 
        scrollable
        max-width="800px"
        v-model="overlay">
      <v-card>
      <v-card-title class="text-h5">
        Availability Form - {{tutor.name}}
      </v-card-title>
      <v-card-text>
        <div>
          <h3>1. {{questions[0].title}}</h3>
          <p>{{formData.isInterested ? "Yes" : "No"}}</p>
        </div>
        <v-divider></v-divider>
        <div>
          <h3>2. {{questions[1].title}}</h3>
          <p v-if="!formData.roles[0]">N/A</p>
          <p>
            <span v-for="role of formData.roles" :key="role">{{role}}, </span>
          </p>
        </div>
        <v-divider></v-divider>
        <div>
          <h3>3. {{questions[2].title}}</h3>
          <p v-if="!formData.learningcenters[0]">N/A</p>
          <p>
            <span v-for="learningcenter of formData.learningcenters" :key="learningcenter">{{learningcenter}}, </span>
          </p>
        </div>
        <v-divider></v-divider>
        <div>
          <h3>4. {{questions[3].title}}</h3>
          <p v-if="!formData.courses[0]">N/A</p>
          <p>
            <span v-for="course of formData.courses" :key="course">{{course}}, </span>
          </p>
        </div>
        <v-divider></v-divider>
        <div>
          <h3>5. {{questions[4].title}}</h3>
          <p>Monday: {{formData.avail.monday || "N/A"}}</p>
          <p>Tuesday: {{formData.avail.tuesday || "N/A"}}</p>
          <p>Wednesday: {{formData.avail.wednesday || "N/A"}}</p>
          <p>Thursday: {{formData.avail.thursday || "N/A"}}</p>
          <p>Friday: {{formData.avail.friday || "N/A"}}</p>
          <p>Saturday: {{formData.avail.saturday || "N/A"}}</p>
          <p>Sunday: {{formData.avail.sunday || "N/A"}}</p>
        </div>
        <v-divider></v-divider>
        <div>
          <h3>6. {{questions[5].title}}</h3>
          <p>{{formData.misc || "N/A"}}</p>
        </div>
      </v-card-text>
      <v-card-actions>
        <v-btn color="error" @click="overlay=!overlay">Make Changes</v-btn>
        <v-spacer></v-spacer>
        <v-btn color="primary"
           @click="submitForm">Submit Form</v-btn>
      </v-card-actions>
      <v-card-text v-if="submissionError" class="error--text">An unknown error occurred. Please contact a staff member.</v-card-text>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import RolesQuestion from './questions/RolesQuestion'
import InterestedQuestion from './questions/InterestedQuestion'
import LearningCentersQuestion from './questions/LearningCentersQuestion'
import CoursesQuestion from './questions/CoursesQuestion'
import MiscQuestion from './questions/MiscQuestion'
import AvailabilityQuestion from './questions/AvailabilityQuestion'
const axios = require("axios")

export default {
  updated(){
    this.checkValidation()
  },
  computed: {
    enabledQuestions: function () {
      return this.questions.slice(0,this.position+1)
    }
  },
  props: {
    tutor:Object,
    jwt:String
  },
  methods:{
    review(){
      this.showReview=true;
      this.overlay=true;
    },
    disableReview(){
      this.enableReview=false;
    },
    async submitForm(){
      try{
        const uri = "https://lorainccc-asc.azurewebsites.net/";
        const config = {
            headers: { Authorization: `Bearer ${this.jwt}` }
        };
        if(this.formData.isInterested){
          const dest = uri+"/api/tutors/"+this.tutor._id+"/courses/";
          const body = this.formData.courses
          const response = await axios.post(dest,body,config)
          if(!(response.status==201||response.status==200)){
            this.submissionError=true;
          }
        }
        
        const flowdest = uri+"/api/tutors/"+this.tutor._id+"/availform/";
        const flowbody = this.formData
        const flowResponse = await axios.post(flowdest,flowbody,config)
        
        if(!(flowResponse.status==201||flowResponse.status==200)){
          this.submissionError=true;
        }

        this.submissionError=false;
        this.$emit("formComplete")
        
      }
      catch(err){
        this.submissionError=true;
      }
    },
    answered(questionNum){
      this.questions.forEach(question => {
        if(question.number === questionNum){
          question.answered = true
        }
      })
    },
    changedInterest(isInterested){
      this.formData.isInterested = isInterested
      if(this.formData.isInterested===false){
        this.questions.forEach(question => {
          if(question.type!=InterestedQuestion){
            question.disabled=true;
          }
        })
        this.enableReview=false;
      }
      else{
        this.questions.forEach(question => {
          question.disabled=false
        })
        this.enableReview=true;
      }
    },
    changedRoles(roles){
      this.formData.roles=roles
    },
    changedLearningCenter(lcs){
      this.formData.learningcenters=lcs
    },
    changedCourses(courses){
      this.formData.courses=courses
    },
    changedAvail(avail){
      this.formData.avail=avail
    },
    changedMisc(misc){
      this.formData.misc=misc
    },
    checkValidation(){
      if(this.isInterested===false){
        this.isComplete=true;
      }
      else{
        this.isComplete=false;
      }
    },
    expandDrawer(position){
      this.position = position
    }
  },
  data () {
    return {
      showReview: false,
      enableReview: true,
      overlay: false,
      questions: [
        {
          "number":0,
          "title":"Are you interested in working next semester?",
          "type":InterestedQuestion,
          "answered":false,
          "disabled":false
        },
        {
          "number":1,
          "title": "What are your role(s) with the ASC?",
          "type":RolesQuestion,
          "answered":false,
          "disabled":true
        },
        {
          "number":2,
          "title":"At which Learning Centers are you willing to work?",
          "type":LearningCentersQuestion,
          "answered":false,
          "disabled":true
        },
        {
          "number":3,
          "title":"What courses are you comfortable tutoring?",
          "type":CoursesQuestion,
          "answered":false,
          "disabled":true
        },
        {
          "number":4,
          "title":"What times are you available to tutor each day?",
          "type":AvailabilityQuestion,
          "answered":false,
          "disabled":true
        },
        {
          "number":5,
          "title":"Is there anything else we should know about your availability? Please also indicate here if you'll be working in another department concurrently this semester.",
          "type":MiscQuestion,
          "answered":false,
          "disabled":true
        }
      ],
      formData: {
        name: this.tutor.name,
        isInterested: null,
        roles: [],
        learningcenters: [],
        courses: [],
        avail: {
          monday:"",
          tuesday:"",
          wednesday:"",
          thursday:"",
          friday:"",
          saturday:"",
          sunday:""
        },
        misc: ""
      },
      isComplete: false,
      position: 0,
      submissionError: false
    }
  }
}
</script>

