<template>
  <v-container>
    <v-checkbox
      v-model="roles"
      label="Tutor"
      value="Tutor"
    ></v-checkbox>
    <v-checkbox
      v-model="roles"
      label="Front Desk"
      value="FrontDesk"
    ></v-checkbox>
    <v-btn
      block
      color="primary"
      elevation="2"
      @click="this.onSubmit"
      v-if="this.question.answered===false"
    >Continue
    </v-btn>
  </v-container>
</template>

<script>
export default {
  props: {
    tutor:Object,
    question:Object
  },
  data () {
    return {
      roles: []
    }
  },
  watch:{
    roles: function(value) {
      this.$emit("changedRoles",value)
    }
  },
  methods:{
    onSubmit(){
        this.$emit('answered',1)
        this.$emit("expandDrawer",2)
    }
  }
}
</script>

