<template>
  <v-container>
    <v-checkbox
      v-model="learningCenters"
      label="UPRC - North Ridgeville Campus"
      value="UPRC"
    ></v-checkbox>
    <v-checkbox
      v-model="learningCenters"
      label="Wellington Campus"
      value="Wellington"
    ></v-checkbox>
    <v-checkbox
      v-model="learningCenters"
      label="Lorain High School Learning Center"
      value="LorainHigh"
    ></v-checkbox>
    <v-checkbox
      v-model="learningCenters"
      label="Lorain Downtown Learning Center"
      value="LorainDowntown"
    ></v-checkbox>
    <v-checkbox
      v-model="learningCenters"
      label="Firelands BGSU"
      value="Firelands"
    ></v-checkbox>
    <v-btn
      block
      color="primary"
      elevation="2"
      @click="this.onSubmit"
      v-if="this.question.answered===false"
    >Continue
    </v-btn>
  </v-container>
</template>

<script>
export default {
  props: {
    tutor:Object,
    question:Object
  },
  data () {
    return {
      learningCenters: []
    }
  },
  watch:{
    learningCenters: function(value) {
      this.$emit("changedLearningCenter",value)
    }
  },
  methods:{
    onSubmit(){
        this.$emit('answered',2)
        this.$emit("expandDrawer",3)
    }
  }

}
</script>

