<template>
  <v-container>
    <v-text-field
      v-model="email"
      :rules="emailRules"
      label="Confirm your employee e-mail"
      required
    ></v-text-field>
    <p v-if="this.currentError" class="error--text">The email address you entered is incorrect.</p>
    <v-btn
      block
      color="primary"
      elevation="2"
      @click="this.confirmEmail"
    >Confirm
    </v-btn>
  </v-container>
</template>

<script>
const axios = require("axios")
export default {
  created()
  {
    const uri = window.location.search.substring(1); 
    const params = new URLSearchParams(uri);
    console.log(params.get("token"));
    this.token = params.get("token")
  },
  data () {
    return {
      email: '',
      emailRules: [],
      token: '',
      currentError: false
    }
  },
  methods:{
    async confirmEmail(){
      try{
        const postData = {
          "email":this.email.toLowerCase(),
          "availCode": this.token
        }
        const uri = "https://lorainccc-asc.azurewebsites.net/";
        const response = await axios.post(uri+"/api/checkEmail",postData)
        if(response.status === 200){
          const tutor = response.data.tutor
          const jwt = response.data.jwt
          this.$emit('emailConfirmed',true)
          this.$emit('updateTutorInfo',tutor)
          this.$emit('jwtAssigned',jwt)
          this.$emit('moveToStep',2)
          this.currentError=false;
        }
        else{
          this.currentError=true;
        }
      }
      catch{
        this.currentError=true;
      }
      
    }
  }
}
</script>

